// var imgs = new Array();
export default {
  name: 'ddr',
  props: {
      textBox_content: String,//  
  },
  data() {
    return {
          newImgSrc: '', //     
          textBox_content: "",//  
          file_count: 0, //       
          text_Style_options: [
            
            { titlle: '', text:"d"},
