            { value: '19px', text: '19'},
            { value: '22px', text: '22'},
            { value: '25px', text: '25'},
            { value: '28px', text: '28'},
            { value: '31px', text: '31'},
            { value: '34px', text: '34'},
            { value: '37px', text: '37'},
            { value: '40px', text: '40'},
            { value: '43px', text: '46'},
            { value: '46px', text: '46'},
            { value: '50px', text: '50'},
          ],
              text_Thickness_options: [
            { value: 'normal', text: ''},
            { value: 'bold', text: ''},
          ],
         postIndex: "0", //     
         file: null,
         albumBucketName : "portfolioserversungkyul",
         bucketRegion : "ap-northeast-2",
         IdentityPoolId : "ap-northeast-2:b491c966-e183-46be-b4ab-ccc670ec5e3d",
         fileList: [], //     
         fileImages: [//      
            {
              name: '',
              image : ''
            },
            {
              name: '',
              image : ''
            },
            {
              name: '',
              image : ''
            },
         ], 
         //      
        //  : [
        //     {
        //       name: '',
        //       video : ''
        //     },
        //     {
        //       name: '',
        //       video : ''
        //     },
        //     {
        //       name: '',
        //       video : ''
        //     },
        // ],
         fileName: "",
         preview: '', //   
        slot_cnt: 0, //  
        slot_show1: false, // 
        slot_show2: false,
        slot_show3: false,
        slot_show4: false,
        slot_show5: false,
        slot_show6: false,
        slot_show7: false,
        postCount:0,//    
        myID: ""
    }
  },
  created() //    
  {
    var login = localStorage.getItem('isLogin'); //   
    if(login == "true") //   
    { 
      //
      this.myID =  localStorage.getItem('userId')
    }
    else //    
    {
     this.myID = ""
    }
    this.AWS_PostCountCheck(); //     
  },
  methods: {
      AWS_GetPost(postIndex) // AWS      (  )
      {
          // AWS   
            AWS.config.update({
            region: this.bucketRegion,
            credentials: new AWS.CognitoIdentityCredentials({
            IdentityPoolId: this.IdentityPoolId
            })
          });

        this.$store.commit('GetAllFiles') //    
        this.postIndex = postIndex

        //   
        this.fileImages = []
       for(var i = 0; i < this.$store.state.awsDatalists.length; i++)
       { 
          //  .    
          var idx = this.$store.state.awsDatalists[i].indexOf("."); 
      
          //   
          var extension =''
          var idxx = this.$store.state.awsDatalists[i].indexOf("*"); 
              if(idxx == -1)//  ,   
              {
                 //   
                 extension = this.$store.state.awsDatalists[i].substr(idx+1,this.$store.state.awsDatalists[i].length);
              }
              else
              {
                 extension = this.$store.state.awsDatalists[i].substr(idx+1,this.$store.state.awsDatalists[i].length-1);
              }
          
          // $    
          var idx = this.$store.state.awsDatalists[i].indexOf("$"); 

          if(idx != -1)
          {
          // id 
          var userId = this.$store.state.awsDatalists[i].substr(0,idx);
          //  index  
          var fileIndex = this.$store.state.awsDatalists[i].substr(idx+1,1);
          }
          else{fileIndex=0}

           // _    
          var idx = this.$store.state.awsDatalists[i].indexOf("_"); 
          if(idx != -1){
              // *  ( ) 
              var idxx = this.$store.state.awsDatalists[i].indexOf("*"); 
              if(idxx == -1){//  ,   
                var fileName = this.$store.state.awsDatalists[i].substr(idx+1, this.$store.state.awsDatalists[i].length)
              }
              else
              {
                var fileName = this.$store.state.awsDatalists[i].substr(idx+1, this.$store.state.awsDatalists[i].length-1)
              }
           
          }
          } 
         //     id   ,     index   .
        //  if(userId != localStorage.getItem('userId')){ 
        //    this.myID=""
        //   return alert ("   .   "+ localStorage.getItem('userId'))}

         if( (userId == "") && (fileIndex == this.postIndex)&& (fileIndex!=0))
         {
         // var id = localStorage.getItem('userId'); //   
         // var tittle_content = id+"$"+ this.postIndex(,1~8)+(0~2) + ".txt"; //  

        if(extension == "txt")//  
            {        
              let params = {Bucket: "portfolioserversungkyul", Key: this.$store.state.awsDatalists[i]} // AWS    
              let s3 = new AWS.S3({ params: { Bucket: "portfolioserversungkyul" }});
   
              s3.getObject(params, (err, data) => {
                    var text = data.Body.toString()          
        
                    //  $    
                    var idx = text.indexOf("$"); 
                    //   
                    const postContent = text.substring(idx+1, text.length)
                    //   
                    const posttitlle = text.substring(0, idx)

                    //   
                    const tittle = document.getElementById('postTittle')
                    tittle.value = posttitlle
                
                    //   
                    const editTextArea = document.getElementById('editTextArea') 
                    editTextArea.value = postContent    
              })
            }
            else if((extension == "jpg")||(extension == "jpeg")||(extension=="png"))
            {
              let params = {Bucket: "portfolioserversungkyul", Key: this.$store.state.awsDatalists[i]} // AWS    
              let s3 = new AWS.S3({ params: { Bucket: "portfolioserversungkyul" }});

              s3.getObject(params, (err, data) => {
            // blobURL
              // Blob javascript , ,       
              const blob = new Blob([data.Body], {
                  type: data.ContentType
                }
              );
              //   
              const blobURL = URL.createObjectURL(blob);   
             //    , *  .
              // *    
              var idx = text.indexOf("*"); 
              if(idx!=-1){ // * ,
                this.postImgName = fileName
                this.postImgURL = blobURL 
              } 
              else{ //   
              this.fileImages.push({name:fileName,image:blobURL})   
              }     
              })
           }
            else if((extension == "m4v")||(extension == "mp4")||(extension=="avi ")
            ||(extension == "wmv")||(extension=="mwa")||(extension == "asf"))
            {           
              let params = {Bucket: "portfolioserversungkyul", Key: this.$store.state.awsDatalists[i]} // AWS    
              let s3 = new AWS.S3({ params: { Bucket: "portfolioserversungkyul" }});
        
              s3.getObject(params, (err, data) => {
                if(err)
                {
                  alert("   .")
                }
                else
                {
                  // Blob javascript , ,       
                    const blob = new Blob([data.Body], {
                        type: data.ContentType
                      }
                    );
                    //   
                  // URL.createObjectURL()     URL DOMString   
                  const blobURL = URL.createObjectURL(blob);   
                  // this.fileVideos.push({name:fileName,image:blobURL})
                  // this.fileIndex=i
                  let _video = document.querySelector('#fileVideo');
                  _video.removeAttribute('src'); // src  
                  _video.src = blobURL; //    
                  _video.load(); //    
                  _video.play(); //    
                }
              })
            }
         }
         }
      },
      AWS_PostCountCheck()
      {
        this.$store.commit('GetAllFiles') //    

        for(var i=0; i<this.$store.state.awsDatalists.length; i++)
        {
            var fileName = this.$store.state.awsDatalists[i]        
            var naem = "" // id 

            for(var j=1; j<=8; j++)
            {
              var a = naem + "$" + j + ".txt"
              if(a == fileName)
                {
                    this.postCount++;
                }
            }
        }
        switch(this.postCount)
        {
          case 2:  this.slot_show1 = true;
          //  
            break;
          case 3:   this.slot_show1 = true;
                    this.slot_show2 = true;
            break;
          case 4:  this.slot_show1 = true;
                   this.slot_show2 = true;
                   this.slot_show3 = true;
            break;
          case 5:  this.slot_show1 = true;
                   this.slot_show2 = true;
                   this.slot_show3 = true;
                   this.slot_show4 = true;
             break;
          case 6:  this.slot_show1 = true;
                   this.slot_show2 = true;
                   this.slot_show3 = true;
                   this.slot_show4 = true;
                   this.slot_show5 = true;
            break;
            case 7: this.slot_show1 = true;
                    this.slot_show2 = true;
                    this.slot_show3 = true;
                    this.slot_show4 = true;
                    this.slot_show5 = true;
                    this.slot_show6 = true;
            break;
            case 8: this.slot_show1 = true;
                    this.slot_show2 = true;
                    this.slot_show3 = true;
                    this.slot_show4 = true;
                    this.slot_show5 = true;
                    this.slot_show6 = true;
                    this.slot_show7 = true;
            break;
            default:
              break;
        }
      },
      LoginCheck_PortfolioList()
      {
        var login = localStorage.getItem('isLogin'); //   

        if(login) //   
        { 
           var name = localStorage.getItem('userName'); //  
          if(name == '')
          {
            var link = 'https://sc9486.github.io/#/PortfolioList';
            location.href = link;
          }
