            location.href=link;
          }
        }
        else //    
        {
          var link = 'https://sc9486.github.io/#/PortfolioList5';
          location.href=link;
        }
      },
        GotoMainPage()
      {
        var link = 'https://sc9486.github.io/#/';
        location.href=link;
      },
}
</script>

