            },
            //     
            AWS_FileUploadCheck: function(event) {
                this.file = event.target.files[0]

                if (this.fileList.length == 1) {
                    return alert("     1.")
                }
                if (!this.file) {
                    return;
                }
                if (this.file.type.match(/^image\/(png|jpeg)$/)) {
                    //   
                    //      URL DOMString    URL.createObjectURL()  
                    this.preview = window.URL.createObjectURL(this.file)
                    this.fileImages.push({
                        name: this.file.name,
                        image: this.preview
                    })

                    var image = new Image();
                    image = this.file
                    imgs.push(image)
                } else if (this.file.type.match(/^video\/(mp4|m4v|avi|wmv|mwa|asf)$/)) {
                    //   
                    this.preview = window.URL.createObjectURL(this.file)

                    //  video.setAttribute("src", this.preview); 
                    // this.fileVideos.push({name:this.file.name,image:this.preview})
                    // const video = document.getElementById("fileVideo");
                    // video.play();

                    let _video = document.querySelector('#fileVideo');
                    _video.removeAttribute('src'); // src  
                    _video.src = this.preview; //    
                    _video.load(); //    

                    _video.play(); //      
                }
                this.fileList.push(this.file.name)
            },
            //     
            AWS_PostImgUploadCheck: function(event) {
                this.postImgFile = event.target.files[0]
                if (!this.postImgFile) {
                    return;
                }
                if (this.postImgFile.type.match(/^image\/(png|jpeg)$/)) {
                    //    
                    //     URL DOMString    URL.createObjectURL() 
                    this.postImgName = this.postImgFile.name
                    this.postImgURL = window.URL.createObjectURL(this.postImgFile)
                    this.fileList.push(this.postImgName)
                }
            },
            // AWS      .   
            AWS_GetFiles() {
                // AWS   
                AWS.config.update({
                    region: this.bucketRegion,
                    credentials: new AWS.CognitoIdentityCredentials({
                        IdentityPoolId: this.IdentityPoolId
                    })
                });
                var s3 = new AWS.S3({
                    apiVersion: "2006-03-01",
                    params: {
                        Bucket: this.albumBucketName
                    }
                });
                this.fileList = [];
                // AWS   (AWS       )
                s3.listObjects({
                    Delimiter: "/"
                }, (err, data) => {
                    if (err) { //    
                        return alert("   ." + err.message);
                    } else //   ,
                    {
                        //fileList = data.Contents;
                        for (var name in data.Contents) {
                            // ,        
                            //     .
                            var searchText = ".txt"
                            var idx = data.Contents[name].Key.indexOf(searchText)

                            if (idx == -1) //   
                            {
                                //          ,
                                //  $   -> $_. =>   
                                var searchText = "$"
                                var s = data.Contents[name].Key.indexOf(searchText)
                                var checkOne = 0;
                                var checkTwo = 0;

                                if (s != -1) // $ 
                                {
                                    checkOne = s;
                                }
                                // $     , $  .

                                //  _    
                                var w = data.Contents[name].Key.indexOf("_");
                                if (w != -1) {
                                    checkTwo = w; // _  .
                                }
                                var gab = checkTwo - checkOne - 1

                                // $ _     .
                                const fileIndex = data.Contents[name].Key.substr(checkOne + 1, gab)

                                //       ,
                                if (this.postIndex == fileIndex) {
                                    // _    
                                    // var w = data.Contents[name].Key.indexOf("_"); 
                                    // if(w != -1)
                                    // {
                                    //    checkTwo = w; // _  .
                                    // }
                                    //   .
                                    const fileName = data.Contents[name].Key.substr(checkTwo + 1)

                                    //     .
                                    this.fileList.push(fileName)
                                }
                                //    console.log("   " +data.Contents[name].Key)
                            }
                        }
                    }
                });
            },
            //     
            AWS_FileUpload() {
                if (this.fileList.length == 0) {
                    return;
                } //    , 
                // AWS   
                AWS.config.update({
                    region: this.bucketRegion,
                    credentials: new AWS.CognitoIdentityCredentials({
