        this.minBoundary = { minLeft, maxLeft, minTop, maxTop }
      }
      document.addEventListener('mousemove', this.handleMouseMoveA, false) // DOM. addEventListener(,  , )
      document.addEventListener('touchmove', this.handleMouseMoveA, false)
